<template>
    <table class="table table-calendar">
        <thead>
            <th v-for="title in head">{{ title }}</th>
        </thead>
        <tbody>
            <tr v-for="week in totalDates">
                <td v-for="timetable in week" :class="dateClasses(timetable.date)" @click="handleChangeDate(timetable.date)">
                    <span class="date">
                        {{ timetable.date.getDate() }}
                    </span>
                    <span class="dot" v-for="i in (1, timetable.count)"></span>
                </td>
            </tr>
        </tbody>
    </table>
</template>

<script>
    export default {
        props: {
            currentDate: {
                type: Date,
                default () {
                    return new Date()
                }
            }
        },
        data () {
            return {
                head: [
                    'Thứ 2',
                    'Thứ 3',
                    'Thứ 4',
                    'Thứ 5',
                    'Thứ 6',
                    'Thứ 7',
                    'Chủ nhật',
                ],
                dayMappingName: ['CN', 'T2', 'T3', 'T4', 'T5', 'T6', 'T7']
            }
        },

        computed: {
            timetables () {
                const dates = this.offsetBefore.concat(this.dates, this.offsetAfter)
                var timetables = this.$store.state.timetables
                var firstDate = dates[0]
                var lastDate = dates[dates.length - 1]

                timetables = timetables.filter(timetable => {
                    let dateFrom = timetable.from.split('/')
                    dateFrom = new Date(dateFrom[2], dateFrom[1] - 1, dateFrom[0])

                    let dateTo = timetable.to.split('/')
                    dateTo = new Date(dateTo[2], dateTo[1] - 1, dateTo[0])

                    if (timetable.day == 'T2' && timetable.from == '11/02/2019') {
                        console.log(timetable.from)
                        console.log(dateFrom)

                        console.log(firstDate)
                    }

                    return !(
                        dateFrom.getTime() < firstDate.getTime() && dateTo.getTime() < firstDate.getTime()
                        || dateFrom.getTime() > lastDate.getTime () && dateTo.getTime() > lastDate.getTime()
                    )
                })

                var timetableMonth = []
                dates.forEach(date => {
                    timetableMonth.push({
                        date: date,
                        count: timetables.filter(timetable => {
                            let dateFrom = timetable.from.split('/')
                            dateFrom = new Date(dateFrom[2], dateFrom[1] - 1, dateFrom[0])

                            let dateTo = timetable.to.split('/')
                            dateTo = new Date(dateTo[2], dateTo[1] - 1, dateTo[0])

                            return (
                                this.dayMappingName[date.getDay()] == timetable.day
                                && dateFrom.getTime() <= date.getTime()
                                && dateTo >= date.getTime()
                            )
                        }).length
                    })
                })

                return timetableMonth
            },

            today () {
                return new Date()
            },

            startOfMonth () {
                return new Date(this.currentDate.getFullYear(), this.currentDate.getMonth(), 1)
            },

            endOfMonth () {
                return new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() + 1, 0)
            },

            dates () {
                var from = new Date(this.startOfMonth.getFullYear() , this.startOfMonth.getMonth(), this.startOfMonth.getDate())
                var to = new Date(this.endOfMonth.getFullYear() , this.endOfMonth.getMonth(), this.endOfMonth.getDate())
                var dates = []
                for (var day = from; day <= to; day.setDate(day.getDate() + 1)) {
                    let date = new Date(day.getFullYear() , day.getMonth(), day.getDate())
                    dates.push(date)
                }

                return dates
            },

            offsetBefore () {
                var offset = this.startOfMonth.getDay()

                if (offset == 0) {
                    offset = 6
                } else {
                    offset--
                }

                var dates = []
                var current = new Date(this.startOfMonth.getFullYear() , this.startOfMonth.getMonth(), this.startOfMonth.getDate())

                for (var i = 0; i < offset; i++) {
                    current.setDate(current.getDate() - 1)
                    let date = new Date(current.getFullYear() , current.getMonth(), current.getDate())
                    dates.unshift(date)
                }

                return dates
            },

            offsetAfter () {
                var offset = this.endOfMonth.getDay()

                if (offset == 0) {
                    offset = 6
                } else {
                    offset--
                }

                offset = 6 - offset
                if (offset == 0) {
                    return []
                }

                var dates = []
                var current = new Date(this.endOfMonth.getFullYear() , this.endOfMonth.getMonth(), this.endOfMonth.getDate())

                for (var i = 0; i < offset; i++) {
                    current.setDate(current.getDate() + 1)
                    let date = new Date(current.getFullYear() , current.getMonth(), current.getDate())
                    dates.push(date)
                }

                return dates
            },

            totalDates () {
                const dates = this.timetables
                var i, j, temparray, chunk = 7
                const chunkedDates = []
                for (i = 0, j = dates.length; i<j; i += chunk) {
                    temparray = dates.slice(i, i + chunk)
                    chunkedDates.push(temparray)
                }
                return chunkedDates
            }
        },

        methods: {
            dateClasses (date) {
                var classes = []
                if (date.getMonth() != this.currentDate.getMonth()) {
                    classes.push('other-month')
                }

                if (
                    date.getFullYear() == this.today.getFullYear()
                    && date.getMonth() == this.today.getMonth()
                    && date.getDate() == this.today.getDate()
                ) {
                    classes.push('today')
                }

                return classes
            },

            handleChangeDate (date) {
                this.$emit('change', date)
            }
        }
    }
</script>