<template>
    <div>
        <template v-if="!timetables">
            <div class="none">
                <p>Bạn chưa cập nhật thời khóa biểu mới. Bấm vào tab "Cập nhật thời khóa biểu mới" và tải lên file excel thời khóa biểu của bạn</p>
            </div>
        </template>
        <template v-else>
            <div class="view-mode">
                <ul>
                    <li :class="{ active: viewMode == 1 }"><a href="" @click.prevent="viewMode = 1">Theo ngày</a></li>
                    <li :class="{ active: viewMode == 3 }"><a href="" @click.prevent="viewMode = 3">Theo tháng</a></li>
                </ul>
            </div>

            <div class="table-container" v-if="viewMode == 1">
                <div class="table-title">
                    {{ timetableTitle }}
                </div>
                <table v-if="timetableByDate.length" class="table">
                    <thead>
                        <th>#</th>
                        <th>Bắt đầu lúc</th>
                        <th>Môn</th>
                        <th>Phòng học</th>
                        <th>Giảng viên</th>
                    </thead>
                    <tbody>
                        <tr v-for="(timetable, index) in timetableByDate">
                            <td style="white-space: nowrap;">{{ timetable.order }}</td>
                            <td>
                                {{ getTimeStart(timetable) }}
                            </td>
                            <td>{{ timetable.subject }}</td>
                            <td>{{ timetable.room }}</td>
                            <td>{{ timetable.lecturer }}</td>
                        </tr>
                    </tbody>
                </table>
                <div v-else class="text-center">
                    Bạn rảnh :))
                </div>
            </div>

            <div class="table-container" v-if="viewMode == 3">
                <div class="table-title">{{ timetableTitle }}</div>
                <Calendar :current-date="currentDate" @change="date => { currentDate = date; viewMode = 1 }" />
            </div>

            <div class="timetable-page">
                <ul v-if="viewMode == 1">
                    <li><a href="" @click.prevent="prevDate">&lt; Ngày trước</a></li>
                    <li><a href="" @click.prevent="goToday">Ngày hôm nay</a></li>
                    <li><a href="" @click.prevent="nextDate">Ngày sau &gt;</a></li>
                </ul>
                <ul v-if="viewMode == 3">
                    <li><a href="" @click.prevent="prevMonth">&lt; Tháng trước</a></li>
                    <li><a href="" @click.prevent="goToday">Tháng này</a></li>
                    <li><a href="" @click.prevent="nextMonth">Tháng sau &gt;</a></li>
                </ul>
            </div>
        </template>

        <div v-html="timetableAfter"></div>
    </div>
</template>
<script>
    import Calendar from './Calendar'

    export default {
        components: {
            Calendar
        },

        data () {
            return {
                viewMode: 1,
                currentDate: new Date(),
                dayMappingName: ['CN', 'T2', 'T3', 'T4', 'T5', 'T6', 'T7']
            }
        },

        created () {
            var now = new Date()
            if (now.getHours() >= 20) {
                now.setDate(now.getDate() + 1)
                this.currentDate = new Date(now.getFullYear(), now.getMonth(), now.getDate())
            }
        },

        computed: {
            isSummner () {
                var summerStartDate = this.$store.state.autoload.time_open_close.summer_start.split('/')
                var summerStartDate = new Date(this.currentDate.getFullYear(), summerStartDate[1] - 1, summerStartDate[0])

                var winterStartDate = this.$store.state.autoload.time_open_close.winter_start.split('/')
                var winterStartDate = new Date(this.currentDate.getFullYear(), winterStartDate[1] - 1, winterStartDate[0])

                return (
                    this.currentDate.getMonth() >= summerStartDate.getMonth()
                    && this.currentDate.getDate() >= summerStartDate.getDate()

                    && this.currentDate.getMonth() <= winterStartDate.getMonth()
                    && this.currentDate.getDate() <= winterStartDate.getDate()
                )
            },

            isWinter () {
                return !this.isSummner
            },

            summer () {
                return this.$store.state.autoload.time_open_close.summer
            },

            winter () {
                return this.$store.state.autoload.time_open_close.winter
            },

            timetableAfter () {
                return this.$store.state.autoload.ads.afterTimetable
            },

            timetableTitle () {
                if (this.viewMode == 1) {
                    var segments = []
                    var today = new Date()

                    if (this.currentDateIsToday) {
                        segments.push('Hôm nay')
                    } else if (this.currentDateIsTomorrow) {
                        segments.push('Ngày mai')
                    } else if (this.currentDateIsYesterDay) {
                        segments.push('Hôm qua')
                    }

                    segments.push(this.dayMappingName[this.currentDate.getDay()])
                    segments.push([this.currentDate.getDate(), this.currentDate.getMonth() + 1, this.currentDate.getFullYear()].join('/'))

                    return segments.join(' ')
                }

                if (this.viewMode == 2) {

                }

                if (this.viewMode == 3) {
                    return ['Tháng', [this.currentDate.getMonth() + 1, this.currentDate.getFullYear()].join('/')].join(' ')
                }
            },

            timetables () {
                return this.$store.state.timetables
            },

            timetableByDate () {
                var dayName = this.dayMappingName[this.currentDate.getDay()]

                return this.timetables.filter(timetable => {
                    let dateFrom = timetable.from.split('/')
                    dateFrom = new Date(dateFrom[2], dateFrom[1] - 1, dateFrom[0])

                    let dateTo = timetable.to.split('/')
                    dateTo = new Date(dateTo[2], dateTo[1] - 1, dateTo[0])
                    return (
                        timetable.day == dayName
                        && dateFrom.getTime() <= this.currentDate.getTime()
                        && dateTo >= this.currentDate.getTime()
                    )
                })
            },

            currentDateIsToday () {
                var today = new Date()
                return (
                    this.currentDate.getDate() == today.getDate()
                    && this.currentDate.getFullYear() == today.getFullYear()
                    && this.currentDate.getMonth() == today.getMonth()
                )
            },

            currentDateIsTomorrow () {
                var today = new Date()
                today.setDate(today.getDate() + 1)
                return (
                    this.currentDate.getDate() == today.getDate()
                    && this.currentDate.getFullYear() == today.getFullYear()
                    && this.currentDate.getMonth() == today.getMonth()
                )
            },

            currentDateIsYesterDay () {
                var today = new Date()
                today.setDate(today.getDate() - 1)
                return (
                    this.currentDate.getDate() == today.getDate()
                    && this.currentDate.getFullYear() == today.getFullYear()
                    && this.currentDate.getMonth() == today.getMonth()
                )
            }
        },

        methods: {
            nextMonth () {
                this.currentDate.setMonth(this.currentDate.getMonth() + 1)
                this.currentDate = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth(), this.currentDate.getDate())
            },

            prevMonth () {
                this.currentDate.setMonth(this.currentDate.getMonth() - 1)
                this.currentDate = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth(), this.currentDate.getDate())
            },

            goToday () {
                this.currentDate = new Date()
            },

            nextDate () {
                this.currentDate.setDate(this.currentDate.getDate() + 1)
                this.currentDate = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth(), this.currentDate.getDate())
            },

            prevDate () {
                this.currentDate.setDate(this.currentDate.getDate() - 1)
                this.currentDate = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth(), this.currentDate.getDate())
            },

            getTimeStart (timetable) {
                try {
                    var first = timetable.order.split('->')[0].trim()

                    if (this.isSummner) {
                        return this.summer[first][0]
                    } else {
                        return this.winter[first][0]
                    }
                } catch (errors) {
                    console.log(errors)
                    return 'Unknow'
                }
            }
        }
    }
</script>