<template>
    <div>
        <div class="upload-zone-container">
            <div class="title">Tải file excel</div>
            <form class="input-file-container" @submit.prevent="submitForm">
                <input type="file" name="file" id="input-file" />
                <button type="submit" class="button-upload">Tải lên</button>
            </form>
        </div>
        <div>
            Chỉ hỗ trợ thời khóa biểu xuất theo thứ. <a :href="excelExampleUrl">Click xem file ví dụ</a>
        </div>
    </div>
</template>
<script>
    import { submit } from '@/api/timetable.js'
    import { mapActions } from 'vuex'

    export default {
        computed: {
            excelExampleUrl () {
                return this.$store.state.autoload.excel_example_url
            }
        },
        methods: {
            async submitForm () {
                var formData = new FormData()
                var file = document.getElementById('input-file').files[0]
                formData.append('file', file)
                const { data } = await submit(formData)
                if (data.success) {
                    this.$emit('update-success')
                    this.setStudent({
                        id: data.data.id,
                        code: data.data.code,
                        name: data.data.name
                    })
                    this.setTimetable(data.data.timetables)
                } else {
                    alert('Có lỗi rồi, vui lòng thử lại');
                }
            },
            ...mapActions(['setStudent', 'setTimetable'])
        }
    }
</script>