import { apiUrl } from '@/helpers/url'
import axios from 'axios'

export const submit = (formData) => {
    return axios.post(apiUrl('timetables'), formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
    })
}

export const get = (studentCode) => {
    return axios.get(apiUrl('timetables/' + studentCode))
}
