<template>
    <div id="app">
        <div class="container" :class="env">
            <div class="tabs">
                <ul>
                    <li :class="{ active: tab == 1 }"><a @click.prevent="tab = 1" href="">Thời khóa biểu</a></li>
                    <li :class="{ active: tab == 2 }"><a @click.prevent="tab = 2" href="">Cập nhật thời khóa biểu mới</a></li>
                    <li :class="{ active: tab == 3 }"><a @click.prevent="tab = 3" href="">{{ aboutTitle }}</a></li>
                    <li v-if="custom.title" :class="{ active: tab == 4 }"><a @click.prevent="tab = 4" href="">{{ custom.title }}</a></li>
                </ul>
            </div>
            <div class="tab-content">
                <timetable v-show="tab == 1" />
                <update-timetable v-show="tab == 2" @update-success="tab = 1" />
                <about v-show="tab == 3" />
                <custom v-show="tab == 4" :content="custom.content" />
            </div>
        </div>
        <div class="copyright">
            <img class="img" src="./assets/img/logo.png" alt="" />
            <span>Extension xem thời khóa biểu cho Sinh viên ICTU</span>
        </div>
    </div>
</template>

<script>
    import Timetable from './components/Timetable'
    import UpdateTimetable from './components/UpdateTimetable'
    import About from './components/About'
    import Custom from './components/Custom'
    import { autoload } from './api/autoload'
    import { mapActions } from 'vuex'

    export default {
        components: {
            Timetable,
            UpdateTimetable,
            About,
            Custom
        },

        data () {
            return {
                tab: 1
            }
        },

        computed: {
            custom () {
                var article = this.$store.state.autoload.article
                return {
                    title: article.title,
                    content: article.content
                }
            },

            studentCode () {
                return this.$store.state.student.code
            },

            timetables () {
                return this.$store.state.timetables
            },

            env () {
                return process.env.NODE_ENV
            },

            aboutTitle () {
                return this.$store.state.autoload.about.title
            }
        },

        async created () {
            this.timetables.length == 0 ? this.tab = 2 : this.tab = 1
            try {
                var { data } = await autoload(this.studentCode)
                if (data.success) {
                    this.setAutoload(data.data)
                }
            } catch (errors) {
                console.log(errors)
            }
        },

        mounted () {
            if (this.env == 'production') {
                var linkTags = document.getElementsByTagName('a')
                for (var link of linkTags) {
                    link.addEventListener('click', e => {
                        e.preventDefault()
                        var url = e.target.getAttribute('href')
                        if (url) {
                            chrome.tabs.create({ url: url })
                        }
                    })
                }
            }
        },

        methods: mapActions(['setAutoload']),
    }
</script>

<style src="./assets/scss/main.scss" lang="scss"></style>
